import React, {Component} from 'react';
import { BrowserRouter as Router, Switch} from 'react-router-dom';
/*------ Pages-----*/
import {Home} from './Pages/Home';
import DigitalMarketing from './Pages/Digital-marketing';
import PaymentProcessing from './Pages/Payment-processing';
import HRManagement from './Pages/HR-Management';
import Startup from './Pages/Startup';
import HomeCRM from './Pages/Home-CRM';
import About from './Pages/About';
import Service from './Pages/Service';
import Process from './Pages/Process';
import Team from './Pages/Team';
import Login from './Pages/Login';
import ResetPassword from './Pages/ResetPassword';
import Register from './Pages/Register';
import SignUpLawyer from './Pages/SignUpLawyer';
import SignUpClient from './Pages/SignUpClient';
import Portfolio2col from './Pages/Portfolio-2col';
import Portfolio3col from './Pages/Portfolio-3col';
import Portfoliofull4col from './Pages/Portfolio-fullwidth-4col';
import PortfolioSingle from './Pages/PortfolioSingle';
import Bloglist from './Pages/Bloglist';
import BlogSingle from './Pages/BlogSingle';
import Contact from './Pages/Contact';
import ScrollToTopRoute from './ScrollToTopRoute';
import Landing from './Pages/Landing';

import CheckoutSuccess from './pagers/CheckoutSuccess';
import TermsOfUse from './pagers/TermsOfUse';
import FixFeesShow from './pagers/FixFeesShow';
import PrivacyPolicy from './pagers/PrivacyPolicy';
import CookiesPreferences from './pagers/CookiesPreferences';
import BecomeInvestor from './pagers/BecomeInvestor';
import BecomeAmbassador from './pagers/BecomeAmbassador';
import Checkout from './pagers/Checkout';

import Jobs from './pagers/Jobs';
import JobDetail from './pagers/JobDetail';
import Contracts from './pagers/Contracts';
import SearchResults from './pagers/SearchResults';
import LawyerProfile from './pagers/LawyerProfile';
import AccountSettings from './pagers/AccountSettings';

class App extends Component{
    componentDidMount() {
      this.props.hideLoader();
  }
  render(){
    return(
        <Router>
           <Switch>
             <ScrollToTopRoute exact={true} path={'/'} component={Home} />
             <ScrollToTopRoute path="/Pages/Digital-marketing" component={DigitalMarketing} />
             <ScrollToTopRoute path="/Pages/Payment-processing" component={PaymentProcessing} />
             <ScrollToTopRoute path="/Pages/HR-Management" component={HRManagement} />
             <ScrollToTopRoute path="/Pages/Startup" component={Startup} />
             <ScrollToTopRoute path="/Pages/Home-CRM" component={HomeCRM} />
             <ScrollToTopRoute path="/Pages/About" component={About} />
             <ScrollToTopRoute path="/Pages/Service" component={Service} />
             <ScrollToTopRoute path="/Pages/Process" component={Process} />
             <ScrollToTopRoute path="/Pages/Team" component={Team} />
             <ScrollToTopRoute path="/Pages/Portfolio-2col" component={Portfolio2col} />
             <ScrollToTopRoute path="/Pages/Portfolio-3col" component={Portfolio3col} />
             <ScrollToTopRoute path="/Pages/Portfolio-fullwidth-4col" component={Portfoliofull4col} />
             <ScrollToTopRoute path="/Pages/PortfolioSingle" component={PortfolioSingle} />
             <ScrollToTopRoute path="/Pages/Bloglist" component={Bloglist} />
             <ScrollToTopRoute path="/Pages/BlogSingle" component={BlogSingle} />
             <ScrollToTopRoute path="/Pages/Contact" component={Contact} />
             <ScrollToTopRoute path="/Pages/Landing" component={Landing} />
             <ScrollToTopRoute path="/Pages/Login" component={Login} />
             <ScrollToTopRoute path="/Pages/ResetPassword" component={ResetPassword} />
             <ScrollToTopRoute path="/Pages/Register" component={Register} />
             <ScrollToTopRoute path="/Pages/SignUpLawyer" component={SignUpLawyer} />
             <ScrollToTopRoute path="/Pages/SignUpClient" component={SignUpClient} />
             <ScrollToTopRoute path="/Pages/checkout" component={Checkout} />
             <ScrollToTopRoute path="/Pages/checkout-success" component={CheckoutSuccess} />
             <ScrollToTopRoute path="/Pages/terms-of-use" component={TermsOfUse} />
             <ScrollToTopRoute path="/Pages/fix-fee-services-show" component={FixFeesShow} />
             <ScrollToTopRoute path="/Pages/privacy-policy" component={PrivacyPolicy} />
             <ScrollToTopRoute path="/Pages/cookies-preferences" component={CookiesPreferences} />
             <ScrollToTopRoute path="/Pages/become-investor" component={BecomeInvestor} />
             <ScrollToTopRoute path="/Pages/become-ambassador" component={BecomeAmbassador} />
             <ScrollToTopRoute path="/Pages/contact" component={ScrollToTopRoute} />
             <ScrollToTopRoute path="/Pages/jobs" component={Jobs} />
             <ScrollToTopRoute path="/Pages/job-detail" component={JobDetail} />
             <ScrollToTopRoute path="/Pages/contracts" component={Contracts} />
             <ScrollToTopRoute path="/Pages/search-results" component={SearchResults} />
             <ScrollToTopRoute path="/Pages/lawyer-profile" component={LawyerProfile} />
             <ScrollToTopRoute path="/Pages/account-settings" component={AccountSettings} />
           </Switch>
        </Router>
    );
  }
}

export default App;
